import React, {useState} from 'react';
import Data from './components/Data';
import Search from './components/Search';

const api = {
  key: "bedfd8f1fa72e543d6e7f6a9f82319dc",
  base: "https://api.openweathermap.org/data/2.5/"
}

function App() {
  const [weather, setWeather] = useState(
    {"coord":{"lon":-74.0817,"lat":4.6097},"weather":[{"id":803,"main":"Clouds","description":"broken clouds","icon":"04d"}],"base":"stations","main":{"temp":14.73,"feels_like":14.4,"temp_min":14.73,"temp_max":14.73,"pressure":1026,"humidity":82},"visibility":10000,"wind":{"speed":4.12,"deg":270},"clouds":{"all":75},"dt":1627163292,"sys":{"type":1,"id":8582,"country":"CO","sunrise":1627123949,"sunset":1627168372},"timezone":-18000,"id":3688689,"name":"Bogota","cod":200}
  );
  const [imgCiudad, setImgCiudad] = useState({
    backgroundImage: `url("https://pixabay.com/get/g8de73d541bc6344e8eecf009dab0cf4af438046bd246782d14ed8191eefcd3ad8cea0946951c53df409e2c6baded3bc8f5d004a70ce74962ec2a716258292815_1280.jpg")`
  });

  const search = query => {
    //Busca datos clima
    fetch(`${api.base}weather?q=${query}&units=metric&APPID=${api.key}`)
    .then(res => res.json())
    .then(result => {
        setWeather(result);
      }
    )

    //Busca imagen en la api de pixabay
    const key = "17268627-4217a51dd9c40ff6690bf88a5";
    const busqueda = query + " city";
    fetch(`https://pixabay.com/api/?key=${key}&q=${busqueda}`)
      .then(response => response.json())
      .then(result => { 
        let imgs = result.hits;
        console.log("1", imgs)
        if(imgs.length === 0){ //Volver a buscar
          fetch(`https://pixabay.com/api/?key=${key}&q=${query}`)
            .then(response => response.json())
            .then(result1 => { 
              let imgs2 = result1.hits;
              console.log("2 ",imgs2);
              if(imgs2.length === 0){ //Volver a buscar
                let res = ((typeof weather.main != "undefined") ? ((weather.main.temp > 18) ? 
                  './assets/warm-bg.jpg' : './assets/cold-bg.jpg') : './assets/warm-bg.jpg')
                  setImgCiudad({
                    backgroundImage: `url("${res}")`
                  });
                } else { //Encontrado
                setImgCiudad({
                  backgroundImage: `url("${imgs2[0].largeImageURL}")`
                });
              }
            })
        } else { //Encontrado
          setImgCiudad({
            backgroundImage: `url("${imgs[0].largeImageURL}")`
          });
        }
      })
  }


  return (
    <div className=
      {(typeof weather.main != "undefined") ? 
        ((weather.main.temp > 18) 
        ? 'app warm'
        : 'app') : 'app'
      }
        style={imgCiudad}>
      <main>
        <h2>Clima Mundial</h2>
        <Search search={search}/>

        <Data weather={weather}/>
        
      </main>
    </div>

  );
}

export default App;
